
.banner{
    color: white;
    object-fit: contain;
    height: 448px;
    max-width: 100%;
  }
  .banner_contents{
    margin-left: 20px;
    padding-top: 9rem;
    height: 190px;
  }
  .banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
  }
  .banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 0.8rem;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
  }
  
  .banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51, 51,51, 0.5);
  }
  
  .banner_button:hover{
    color: black;
    background-color: #e6e6e6;
    transition: all 0.2s;
  }
  .banner .banner--fadeBottom{
    height: 7.4rem;
    margin-top: 140px;
    background-image: linear-gradient(
      180deg,transparent,rgba(37,37,37,0.61),#111
    );
  }